.registration-form-container {
    margin: 100px 0 0 20px;
    max-width: 400px;
    }
    
    .registration-title {
        font-family: "Jost", sans-serif;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.2;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: rgba(252, 132, 45, 1);
        padding-bottom: 50px;
        ;
    }
    
    .registration-form {
        max-width: 400px;
        margin: 0 0;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.2;
        letter-spacing: 0.04em;
        padding: 0;
        display: flex;
        flex-direction: column;
        gap: 20px;
      }
      
      .registration-form-group {
        margin-bottom: 20px;
        position: relative;
      }
      
      .form-label {
        position: absolute;
        top: 0;
        left: 5px;
        font-size: 14px;
        color: rgba(155, 159, 170, 1);
        pointer-events: none;
        transition: 0.2s ease all;
      }
      
      .form-input {
        width: 100%;
        padding: 7px 0;
        row-gap: 20px;
        border: none;
        border-bottom: 1px solid rgba(224, 224, 224, 1);
        background-color: transparent;
        outline: none;
        font-size: 16px;
        cursor:text;
      }
      
      .form-input:focus + .form-label,
      .form-input:not(:placeholder-shown) + .form-label,
      .form-input:valid + .form-label {
        top: -20px;
        left: 0;
        font-size: 12px;
        color: rgb(0, 0, 0);
      }
      
      .login-button,
      .register-button {
        width: 188px;
        height: 44px;
        font-size: 16px;
        background-color: rgba(252, 132, 45, 1);
        color: rgba(255, 255, 255, 1);
        border: transparent;
        border-radius: 30px;
        cursor: pointer;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.2;
        letter-spacing: 0.04em;
        margin: 0;
      }
      
      .login-button {
        background-color: transparent;
        border: 2px solid rgba(252, 132, 45, 1);
        color: rgba(252, 132, 45, 1);
        margin-left: 15px;
      }
      
      .login-button:hover,
      .register-button:hover {
        box-shadow: 0px 4px 10px 0px rgba(252, 132, 45, 0.5);
    
      }
      
      .active {
        color: rgba(33, 33, 33, 1);
      }
      
    
      @media screen and (max-width: 600px) {
        .registration-form-container {
            margin: 50px 0 0 0;
            max-width: 280px;
            display: flex;
            flex-direction: column;
            align-items: center;
            }
    
            .registration-form-group {
                max-width: 280px;
                display: flex;
                gap: 20px;
                flex-wrap: wrap;
                justify-content: center;
                margin-top: 20px;
            }
    
            .login-button,
            .register-button{
                margin: 0;
            }
      }